<template>
  <div id="login">
    <el-form :inline="true" :model="searchForm" label-width="80px">
      <el-form-item label="用户名">
        <el-input v-model="searchForm.userName" placeholder="请输入用户名"></el-input>
      </el-form-item>
      <el-form-item label="密码">
        <el-input v-model="searchForm.password" type="password" placeholder="请输入密码"></el-input>
      </el-form-item>
      <el-form-item class="operation_btn">
        <el-button type="primary" size="medium" @click="login">登录</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {postLogin} from '@/api/login'
import Cookies from 'js-cookie'
export default {
  name: 'login',
  data () {
    return {
      searchForm: {
        userName: '',
        password: ''
      }
    }
  },
  methods: {
    login () {
      this.postLoginFun(this.searchForm)
    },
    async postLoginFun (params) {
      let {data} = await postLogin(params)
      if (data.code === 1 && data.results) {
        Cookies.set('loginStatus', true)
        this.$router.push({name: 'average'})
      } else {
        this.$message({
          message: data.message,
          showClose: true,
          type: 'error'
        })
      }
    }
  }
}
</script>

<style scoped lang="scss">
  #login {
    padding-top: .2rem;
  }
  .operation_btn {
    margin-left: 80px;
  }
</style>



